body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
/*================================ 
colors : 
sunset orange : #f25f5c
dark liver : #50514f
anti flash white : #f2f2f2
white : #ffffff
black : #000000
================================ */

/*===========================================
 html, body 
 ============================================*/

 html,
 body {
     height: 100%;
 }
 
 body {
     color: #50514f;
 }
 
 h1,
 h2,
 h3,
 h4,
 h5,
 h6 {
     font-family: "Raleway", sans-serif;
 }
 
 p {
     font-family: "Open Sans", sans-serif;
     font-size: 13px;
     font-weight: 400;
     line-height: 24px;
 }
 
 
 .section {
     background: #fff;
     overflow: hidden;
 }

.main{
    height: 100%;
}
#root{
    height: 100%;
}
#home {
    background: none;
    height: 100%;
}

#home-bg-video {
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translateX(-50%) translateY(-50%);
    transform: translateX(-50%) translateY(-50%);
    min-height: 100%;
    min-width: 100%;
    height: auto;
    width: auto;
    z-index: -1;
    /* in case video not working */
    background: url(/static/media/home.fc32eec6.png) no-repeat;
    background-size: cover;

}

#home-overlay {
    background-color: rgba(0, 0, 0, 0.8);
    /* black color with overlay; */
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
}

#home-content {
    height: 100%;
    width: 100%;
    display: table;
    
}

#home-content-inner {
    display: table-cell;
    vertical-align: middle;
}

#home-heading h1 {
    color: #fff;
    font-size: 65px;
    font-weight: 100;
    text-transform: uppercase;
    margin: 0;
    display: inline-block;
}

#home-heading-2 span {
    color: #f25f5c;
    font-weight: 500;
}

#home-text p {
    color: #fff;
    font-size: 17px;
    font-weight: 100;
    margin: 8px 0 30px 0;
}
 
 /* buttons */
 
 .btn-general {
    font-family: 'Raleway', sans-serif;
    border-radius: 28px;
    font-size: 13px;
    text-transform: uppercase;
    margin: 0 6px;
    padding: 12px 46px;
    -webkit-transition: all .5s;
    transition: all .5s;
}

.btn-home {
    color: #fff;
    border: 1px solid #fff;
}

.btn-home:hover,
.btn-home:focus {
    color: #fff;
    background-color: #f25f5c;
    border: 1px solid #f25f5c;
}

.btn-orange {
    color: #fff;
    background-color: #f25f5c;
    border: 1px solid #f25f5c;
}

.btn-orange:hover,
.btn-orange:focus {
    color: #fff;
    background-color: #50514f;
    border: 1px solid #50514f;
}
